.home-facebook-logo {
  width: 50%;
  height: 50%;
  padding: 10px;
}

.home-header-container,
.home-above-content,
.home-story-content {
  background-color: white;
}

.circle-container {
  &.mini {
    width: 30px;
    height: 30px;
  }

  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background-color: #e5e5ed;
}

.home-icon-container {
  color: black;
  position: relative;
  float: right;
  margin: 10px 10px 0px 0px;
}

.home-header-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
}

.home-profile-image-container {
  width: 60px;
  height: 60px;
  margin: 10px;
}

.home-profile-comment-container {
  display: flex;
}

.home-profile-comment-input {
  margin: auto 0px;
  border-width: 0px;
  height: 30px;
  padding: 10px;
  color: black;

  &::placeholder {
    color: black;
    opacity: 1;
  }

  &:focus {
    outline-width: 0px;
  }
}

.home-option-container {
  display: flex;
  justify-content: space-between;
  line-height: 3.2;
}

.home-option-icon-container {
  border-top: #e5e5ed solid 1px;
  width: 100%;
  height: 50px;
  text-align: center;
  position: relative;
}

.home-option-icon {
  width: 20px;
  height: 20px;
}

.home-option-icon-border {
  position: absolute;
  right: 0;
  background-color: #e5e5ed;
  top: 50%;
  width: 2px;
  height: 20px;
  transform: translate(-50%, -50%);
}

