@mixin bg-white {
  background-color: white;
}

@mixin stdButton {
  border-width: 0px;
  border-radius: 5px;
  padding: 10px;
  font-weight: bold;
}

.profile-container {
  height: 1000px;
}

.profile-header {
  @include bg-white;
  text-align: center;
  font-size: 18px;
  padding: 10px 0px;
  border-bottom: 1px solid #e5e5ed;
}

.profile-image-container {
  @include bg-white;
  position: relative;
  text-align: center;
  padding: 10px 0px;
  height: 200px;

  > img.profile-timeline {
    width: 100%;
    height: 140px;
    border-radius: 10px 10px 0px 0px;
  }

  > img.profile-image {
    width: 120px;
    height: 120px;
    position: absolute;
    left: 50%;
    top: 150px;
    border-radius: 120px;
    border: 5px solid white;
    transform: translate(-50%, -50%);
  }
}

.profile-name {
  @include bg-white;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 0px;
}

.profile-button-container {
  @include bg-white;
  text-align: center;
  padding: 10px;

  > .story-button {
    @include stdButton;
    background-color: #1c76f2;
    color: white;
    width: 220px;
  }

  > .dot-button {
    @include stdButton;
    background-color: #e5e5ed;
    width: 40px;
    margin-left: 10px;
    line-height: -1;
  }
}

.profile-container {
  @include bg-white;
  padding: 10px;

  .detail-topic {
  }

  .detail-text {
    font-weight: bold;
  }
}
